@import url(https://fonts.googleapis.com/css?family=Dosis&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    position: relative;
    text-align: center;
    color: #e9f7ff;
}

.image img {
    opacity: 0.6;
    background: url(/react-website/static/media/Photo.2591304a.jpg);
    background-size: cover;
}
.Home {
    position: absolute;
    left: 10%;
    top: 40%;
    width: 100%;
    text-align: center;
    font-size: 40px;
    font-family: 'Dosis', sans-serif;
    color: #e9f7ff;
}
.Home img {
    opacity: 0.7;
}
.About {
    position: absolute;
    left: 10%;
    top: 40%;
    width: 100%;
    text-align: center;
    font-size: 40px;
    font-family: 'Dosis', sans-serif;
    color: #e9f7ff;
}
.images img{
    position: absolute;
    left: 10%;
    top: 40%;
    width: 30%;
}

h4 {
    position: absolute;
    left: 10%;
    top: 80%;
    width: 30%;
    text-align: center;
    font-size: 30px;
    font-family: 'Dosis', sans-serif;
    color: #e9f7ff;
}
.Contact {
    position: absolute;
    left: 10%;
    top: 40%;
    width: 100%;
    text-align: center;
    font-size: 40px;
    font-family: 'Dosis', sans-serif;
    color: #e9f7ff;
}

.navbar {
    position: absolute;
    top: 8px;
    left: 16px;
    font-family: 'Dosis', sans-serif;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.navbar h3{
    margin-left: 250px;
    align-self: center;
}

a:link {
    color: #374852;
    text-decoration: none;
}

a:visited {
    color: #374852;
    text-decoration: none;
}


