@import url('https://fonts.googleapis.com/css?family=Dosis&display=swap');

.navbar {
    position: absolute;
    top: 8px;
    left: 16px;
    font-family: 'Dosis', sans-serif;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

